.appWrapper {
    height: 100vh;
    width: 100%;
    z-index: 0;
    position: relative;
    overflow: hidden;
}

.appWrapper::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    /* background-image: url('../images/background/light_background-2.webp'); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 1;
}

/* app sidebar */
.appSideBar {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: var(--app-sizebar-width);
    z-index: 1;
    background-color: var(--sidebar-bg-color);
    padding-top: var(--app-sidebar-padding);
    opacity: 1;
    transform: translateX(0);
    transition: transform linear 200ms 200ms, opacity linear 200ms 200ms;
}

