    opacity: 1;
    transform: translateX(0px);
} */

.appSideBar-transition {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.appSideBar-profile {
    display: flex;
    align-items: center;
    padding: 0 1px;
}

