.appSideBar-profile-content {
    width: 100%;
    padding-left: var(--app-sidebar-item-header-padding);
    padding-right: calc(var(--app-sidebar-item-header-padding) / 2);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 0 16px 0;
}

.appSideBar-profile-heading {
    font-family: var(--default-font-family); 
    font-size: 2.6rem;
    line-height: 2.6rem;
    font-weight: 700;
    color: var(--brand-color);
}

.appSideBar-profile-content-pin {
    align-self: flex-start;
}

.headerIconButton {
    transform: translateY(-3px);
    color: var(--add-card-label-color);
    background-color: transparent;
    border: none;
    opacity: 1;
    display: grid;
    place-content: center;
    padding: 1px;
    cursor: pointer;
    border-radius: 50%;
    transition: color linear 300ms, background-color linear 300ms;
}

.headerIconButton:hover {
    color: var(--text-color-2);
    background-color: var(--card-item-bg-highlighted-color);
}

.headerIconButton svg {
    transform-origin: center;
    transform: scale(0.75);
    transition: transform linear 100ms;
}

.headerIconButton:active svg{
    transform-origin: center;
    transform: scale(0.9);
}

.headerIconButton.headerIconButton--active {
    color: var(--action-color);
}

.headerIconButton:hover.headerIconButton--active {
    color: var(--action-color);
    background-color: var(--card-item-bg-highlighted-color);
}

