.appSideBar__list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.appSideBar__item {
    height: 100%;
    width: 100%;
    position: relative;
}

.appSideBar__taskList {
    list-style-type: none;
}

.appSideBar__taskItem_link {
    width: 100%;
    display: inline-flex;
    text-decoration: none;
}

.appSideBar-scrollShadow {
    height: 0;
    position: relative;
}

.appSideBar-scrollShadow_shadow {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: var(--box-shadow-height);
    background: linear-gradient(to top, var(--box-shadow-bottom-transparent-color), var(--box-shadow-top-dull-color));
}

.appSideBar-scrollShadow_shadow--active {
    display: block;
}

.appSideBar-nav {
    position: relative;
    flex: 1;
    padding: 1px;
    overflow-y: hidden;
}

.appSideBar-wrapper {
    height: 100%;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: var(--scrollbar-thumb-bg-color) var(--scrollbar-pane-bg-color);
    scrollbar-width: thin;
}

.appSideBar-wrapper::-webkit-scrollbar {
    width: var(--scrollbar-width);
}

.appSideBar-wrapper::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-thumb-bg-color);
    border-radius: var(--scrollbar-border-radius);
}

.appSideBar-wrapper::-webkit-scrollbar-track {
    background-color: var(--scrollbar-pane-bg-color);
    border-radius: var(--scrollbar-border-radius);
}

.appSideBar__taskItem {
    width: 100%;
    height: var(--app-sidebar-item-height);
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    transition: background-color 100ms linear;
}

.appSideBar__taskItem:hover {
    background-color: var(--item-hover-bg-color);
}

.appSideBar__taskItem_link {
    flex: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    display: flex;
    align-items: center;
    background-color: transparent;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 2.1rem;
    text-decoration: none;
    white-space: nowrap;
    color: inherit;
}

.appSideBar__taskItem_icon {
    margin-right: var(--app-sidebar-category-icon-margin);
    margin-left: var(--app-sidebar-padding);
    width: var(--app-sidebar-icon-container-size);
    display: grid;
    align-content: center;
    justify-content: left;
}

/* target animation */
#circle-1, #circle-2, #circle-3 {
    stroke: var(--text-color-1);
}

.appSideBar__taskItem_link:hover  #circle-1, .appSideBar__taskItem_link:hover  #circle-2, .appSideBar__taskItem_link:hover  #circle-3 {
    opacity: 0;
    transform-origin: center;
    animation: fadeIn ease 500ms forwards;
    animation-delay: calc(var(--order) * 200ms);
}

#circle-1  {
    stroke: rgb(177, 28, 177);
}

#circle-2  {
    stroke: green;
}

#circle-3  {
    stroke: rgb(181, 117, 0);
}

.appSideBar__taskItem--selected #circle-1,
.appSideBar__taskItem--selected #circle-2,
.appSideBar__taskItem--selected #circle-3 {
    stroke: var(--action-color) !important;
}


@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

