/* calendar animation */
#calendar {
    stroke-width: 1.6;
}

#calendar #number_1,
#calendar #number_2,
#calendar #number_3,
#calendar #number_4,
#calendar #number_5,
#calendar #number_6,
#calendar #number_7 {
    transform: translateX(calc(var(--order) * -12px));
}

#calendar #number_1 path,
#calendar #number_2 path,
#calendar #number_3 path,
#calendar #number_4 path,
#calendar #number_5 path,
#calendar #number_6 path,
#calendar #number_7 path {
    stroke: #36AE7C;
}

#rec_1 path {
    stroke: #C70A80;
}

#line_1 path {
    stroke: #FF2442;
}

#line_2 path {
    stroke: #FF2442;
}

#line_3 path {
    stroke: #F24C4C;
}

.appSideBar__taskItem--selected path {
    stroke: var(--action-color) !important;
}

.appSideBar__taskItem_link:hover #number_1,
.appSideBar__taskItem_link:hover #number_2,
.appSideBar__taskItem_link:hover #number_3,
.appSideBar__taskItem_link:hover #number_4,
.appSideBar__taskItem_link:hover #number_5,
.appSideBar__taskItem_link:hover #number_6,
.appSideBar__taskItem_link:hover #number_7 {
    transform-origin: center;
    animation-delay: 200ms;
    animation: slideRightToLeft ease-out 900ms forwards;
}

@keyframes slideRightToLeft {
    from {
        transform: translateX(calc(var(--order) * -12px + 3 * 12px));
    }

    to {
        transform: translateX(calc(var(--order) * -12px));
    }
}
