/* text_file animation */
#text_file {
    stroke-width: 1.6;
}

.appSideBar__taskItem_link:hover #text_file {
    transform-origin: 50% 80%;
    animation-delay: 200ms;
    animation: swingBackAndForth cubic-bezier(.43,-0.06,.23,1.03) 900ms forwards;
}

@keyframes swingBackAndForth {
    0% {
        transform: rotate(0);
    }

    30% {
        transform: rotate(-10deg);
    }

    60% {
        transform: rotate(6deg);
    }

    100% {
        transform: rotate(0);
    }
}

.appSideBar__taskItem_link:hover path#textline_1 {
    animation-delay: 200ms;
    animation: path_1 ease-in 900ms forwards;
}

@keyframes path_1 {
    0% {

        d: path('M6.5 17H7');
    }

    30% {

        d: path('M6.5 17H7');
    }

    80% {

        d: path('M6.5 17H11.5');
    }

    100% {

        d: path('M6.5 17H11.5');
    }
}

.appSideBar__taskItem_link:hover path#textline_2 {
