}

@keyframes path_2 {
    0% {
        d: path('M6.5 12C6.83333 12 6.5 12 7 12');
    }

    30% {

        d: path('M6.5 12C6.83333 12 6.5 12 7 12');
    }

    80% {

        d: path("M6.5 12C6.83333 12 14.3056 12 17.5 12");
    }

    100% {

        d: path("M6.5 12C6.83333 12 14.3056 12 17.5 12");
    }
}

.appSideBar__taskItem_link:hover path#textline_3 {
    animation-delay: 200ms;
