    cursor: pointer;

    color: var(--text-color-2);
    font-weight: 600;
}

.taskDetail__close-icon svg {
    transform: scale(0.8);
    stroke-width: 2;
    stroke: var(--text-color-2);
    transition: stroke linear 200ms, transform linear 200ms;
}

.taskDetail__close-icon:hover svg {
    stroke: var(--action-color);
}

.taskDetail__close-icon:active svg {
    stroke: var(--action-color);
    transform: scale(0.9);
}

.taskDetail__taskContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.taskDetail__content {
    width: 100%;
}

.taskDetail__taskTitle {
    padding: 16px 0 0 0;
    margin: 0 -8px 16px;
}

.textAreaWrapper {
    padding: 4px 8px;
    width: 100%;
    height: 100%;
    display: grid;
    align-content: center;
    border-radius: var(--task-border-radius);
    background-color: transparent;
    transition: background-color linear 200ms;
}

.textAreaWrapper:hover {
    background-color: var(--textfield-bg-focus-color);
}

.textAreaWrapper:focus-within {
    background-color: var(--textfield-bg-focus-color);
}

.textArea {
    height: var(--detailtask-textarea-height);
    display: block;
    box-sizing: border-box;
    background-color: transparent;
    caret-color: var(--action-color);
    font-size: 2.4rem;
    line-height: 2.8rem;
    font-weight: 600;
    resize: none;
    border: none;
    outline: none;
    scrollbar-width: none;
}

.textArea::-webkit-scrollbar {
    width: 0px;
}

.textArea::placeholder {
    color: var(--add-card-placeholder-color-2);
}

.taskDetail__editTag {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 16px;
    margin-bottom: 16px;
}

.inlineReminder {
    display: grid;
    place-content: center;
}

.inlineReminder input[type="date"] {
    height: var(--card-button-size);
    background-color: var(--card-bg-1-color);
    box-shadow: var(--pane-box-shadow);
    display: block;
    padding: 0 12px;
    border: none;
    outline: none;
    border-radius: var(--card-button-size);
    transition: border linear 200ms;
    cursor: pointer;

    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.1;
    color: var(--text-color-1);
}

.inlineReminder input[type="date"]::-webkit-calendar-picker-indicator {
    background-color: var(--input-date-color);
    padding: 3px;
    border-radius: 5px;
    cursor: pointer;
}


.taskDetail_button {
    height: var(--card-button-size);
    border-radius: var(--card-button-size);
    background-color: var(--card-bg-1-color);
    box-shadow: var(--pane-box-shadow);
    padding: 0 12px;
    border: none;
    outline: none;
    cursor: pointer;

    display: flex;
    align-items: center;
    gap: 5px;

    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.1;
    color: var(--text-color-1);
    transition: color ease 100ms;
}

.taskDetail_button:active {
    color: var(--action-color);
}

.taskDetail_button svg path {
    stroke-width: 1.8;
}

.project .taskDetail_button svg path {
    stroke: var(--task-project-icon-color);
}

.priority .taskDetail_button svg path {
    stroke: var(--task-priority-icon-color);
}

.checkedTask .iconButton {
    display: grid;
    place-content: center;
    padding: 0;
}

.checkedTask .iconButton svg {
    transform-origin: center;
    transform: scale(0.9);
    transition: transform ease 100ms;
}

.checkedTask .iconButton svg path,
.checkedTask .iconButton svg polyline {
    stroke-width: 1.8;
    stroke: var(--badge-text-color);
    transition: stroke ease 100ms;
}

.checkedTask .iconButton:active svg {
    transform-origin: center;
    transform: scale(1);
}

.checkedTask .iconButton:active svg path,
.checkedTask .iconButton:active svg polyline {
    stroke: var(--confirm-color);
}

.checkedTask .iconButton.iconButton--checked svg path,
.checkedTask .iconButton.iconButton--checked svg polyline {
    stroke: var(--confirm-color);
}

.taskDetail__editNotes {
    width: 100%;
}

.notesHeading {
    margin-bottom: 8px;
    text-transform: uppercase;
    color: var(--text-color-1);
    font-size: 1.2rem;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 1.15;
}

.notesArea {
    margin: 0 -8px;
}

.textAreaNotes {
    height: var(--detailtask-textareaNotes-height);
    display: block;
    box-sizing: border-box;
    background-color: transparent;
    caret-color: var(--action-color);
    font-size: 1.8rem;
    line-height: 2rem;
    font-weight: 500;
    resize: none;
    border: none;
    outline: none;
    scrollbar-width: none;
}

.textAreaNotes::-webkit-scrollbar {
    width: 0px;
}

.textAreaNotes::placeholder {
    color: var(--add-card-placeholder-color-2);
}

